/*** Global Styles ***/

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  font-size: 62.5%;
  line-height: 1.6;
  margin: 0;
  color: white;
}

h1, h2, h3 {
  text-align: center;
}

h1 {
  font-family: 'Nosifer', cursive;
  font-size: 5rem;
  margin: 0;
  padding-top: 4rem;
}

h2 {
  font-size: 3rem;
}

h3, .Winner-fontSpan {
  font-family: 'Metal Mania', cursive;
  letter-spacing: 2px;
}

h3 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

p, option, select, button, li {
  font-size: 1.8rem;
}

.Winner-fontSpan {
  border-bottom: 1px solid gold;
}

option {
  color: black;
}

img {
  display: block;
  width: 100%;
  border-radius: 2rem;
  box-shadow: 1px 2px 8px 11px white;
}

button {
  padding: 1rem;
  border-radius: 0.6rem;
  border: 2px solid gold;
  color: white;
  background-color: transparent;
  font-weight: bold;
  cursor: pointer;
  transition: all 500ms;
}

button:hover, button:focus, button:active {
  border: 2px solid white;
  border-radius: 2rem;
}

.arrow {
  position: absolute;
  top: 5rem;
  left: 9rem;
  font-size: 4rem;
  color: gold;
}

@media (max-width: 880px) {
  .arrow {
    left: 11rem;
  }
}

@media (max-width: 770px) {
  button {
    padding: 0.6rem;
  }

  h1 {
    font-size: 4rem;
  }

  h2, h3 {
    font-size: 2rem;
  }
}