html { box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.visuallyhidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;-webkit-clip-path: inset(100%);clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

/*** Global Styles ***/

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  font-size: 62.5%;
  line-height: 1.6;
  margin: 0;
  color: white;
}

h1, h2, h3 {
  text-align: center;
}

h1 {
  font-family: 'Nosifer', cursive;
  font-size: 5rem;
  margin: 0;
  padding-top: 4rem;
}

h2 {
  font-size: 3rem;
}

h3, .Winner-fontSpan {
  font-family: 'Metal Mania', cursive;
  letter-spacing: 2px;
}

h3 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

p, option, select, button, li {
  font-size: 1.8rem;
}

.Winner-fontSpan {
  border-bottom: 1px solid gold;
}

option {
  color: black;
}

img {
  display: block;
  width: 100%;
  border-radius: 2rem;
  box-shadow: 1px 2px 8px 11px white;
}

button {
  padding: 1rem;
  border-radius: 0.6rem;
  border: 2px solid gold;
  color: white;
  background-color: transparent;
  font-weight: bold;
  cursor: pointer;
  transition: all 500ms;
}

button:hover, button:focus, button:active {
  border: 2px solid white;
  border-radius: 2rem;
}

.arrow {
  position: absolute;
  top: 5rem;
  left: 9rem;
  font-size: 4rem;
  color: gold;
}

@media (max-width: 880px) {
  .arrow {
    left: 11rem;
  }
}

@media (max-width: 770px) {
  button {
    padding: 0.6rem;
  }

  h1 {
    font-size: 4rem;
  }

  h2, h3 {
    font-size: 2rem;
  }
}
.App-container {
  min-height: 100vh;
  background-image: url('/versusBackground.jpg');
  background-size: cover;
  background-position: center;
}

@media (max-width: 770px) {
  .App-container {
    background-image: url('/backgroundMobile.jpg');
    min-height: 1300px;
  }
}
.ModalComp-modalBody {
  background-color: black;
  font-size: 1.8rem;
  max-width: 500px;
  margin: 5rem auto;
  padding: 3rem 3rem 5rem;
  border: 1px solid gold;
  text-align: center;
}

.ModalComp-modalBody ol {
  text-align: left;
}
header {
  text-align: center;
}

.Header-main {
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.Header-results {
  min-height: 15vh;
}

@media (max-width: 770px) {
  .Header-main {
    justify-content: space-evenly;
    min-height: 240px;
  }

  .Header-main button {
    margin-top: 2rem;
  }

  .Header-results {
    min-height: 200px;
  }
}
.SelectHeroes-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

@media (max-width: 880px) {
  .SelectHeroes-container {
    padding: 0 2rem;
  }
}
.BattleCondition-container {
  position: relative;
}

@media (max-width: 770px) {
  .BattleCondition-container {
    /* padding-top: 15rem; */
    -webkit-transform: translateY(15rem);
            transform: translateY(15rem);
  }
}
.Dropdown-heroesContainer {
  min-height: 50vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.Dropdown-battleContainer {
  align-self: center;
  -webkit-transform: translateY(-8rem);
          transform: translateY(-8rem);
}

form {
  display: flex;
  flex-direction: column;
}

@media (max-width: 880px) {
  .Dropdown-heroesContainer {
    justify-content: space-between;
  }
}

@media (max-width: 770px) {
  .Dropdown-heroesContainer {
    flex-direction: column;
    min-height: 630px;
    padding-top: 13rem;
  }
}
.Winner-resultsContainer {
  display: flex;
  justify-content: space-around;
}

.Winner-heroContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Winner-imgCircleContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Winner-imgContainer {
  width: 230px;
  margin: 1rem 0 3rem;
  /* align-self: start; */
}

/* drawing the circles */
.Winner-circleContainer {
  width: 150px;
  height: 150px;
  position: relative;
}

/* making it so we can see the inside of the circle */
.Winner-svgGroup {
  fill: none;
  stroke: none;
}

/* drawing labels inside the circles */
.Winner-circleLabel {
  position: absolute;
  width: 150px;
  height: 150px;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
}

/* the circle that represents 100% */
.Winner-baseCircle {
  stroke-width: 7px;
  stroke: grey;
}

/* this will represent the percentage of the powerstat */
.Winner-secondCircle {
  stroke: white;
  stroke-width: 7px;
  stroke-linecap: round;
}


.Winner-grayscale {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.Winner-resetContainer {
  max-width: 11rem;
  margin: 0 auto;
  -webkit-transform: translateY(-11rem);
          transform: translateY(-11rem);
}

@media (max-width: 770px) {
  .Winner-resultsContainer {
    /* margin-top: 5rem; */
    min-height: 800px;
    justify-content: space-between;
  }

  .Winner-resultsContainer {
    flex-direction: column;
  }

  .Winner-imgCircleContainer {
    flex-direction: row;
  }

  .Winner-imgContainer {
    width: 160px;
    padding-right: 3rem;
  }

  .Winner-circleContainer, .Winner-circleLabel {
    width: 90px;
    height: 90px;
  }

  .Winner-circleLabel {
    font-size: 1.8rem;
  }

  .Winner-resetContainer {
    -webkit-transform: translateY(4rem);
            transform: translateY(4rem);
  }
}
